import React, {useMemo, useState, useCallback} from 'react';
import {StaticMap} from 'react-map-gl';
import DeckGL from '@deck.gl/react';
import {ScatterplotLayer, ArcLayer} from '@deck.gl/layers';
import {HeatmapLayer} from '@deck.gl/aggregation-layers';
import {BrushingExtension} from '@deck.gl/extensions';
import {scaleLinear, scaleSqrt, scalePow} from 'd3-scale';

const MAPBOX_TOKEN = process.env.REACT_APP_MapboxAccessToken; // eslint-disable-line

export const inFlowColors = [[35, 181, 184]];
export const outFlowColors = [[166, 3, 3]];

// migrate out
const SOURCE_COLOR = [166, 3, 3];
// migrate in
const TARGET_COLOR = [35, 181, 184];


const brushingExtension = new BrushingExtension();


function getTooltip({object}) {
  return (
    object && {
      html: `<h3>${object.name}</h3><p>Number of collaborations: ${object.net}</p>`,
      style: {fontSize: '0.8em'}
    }
  );
}

/* eslint-disable react/no-deprecated */
export default function App({
  data,
  enableBrushing = true,
  brushRadius = 100,
  strokeWidth = 2,
  opacity = 0.7,
  mapStyle = 'mapbox://styles/mapbox/light-v9'
}) {
  const [initialViewState, setInitialViewState] = useState({
    longitude: -2.9,
    latitude: 56.1,
    zoom: 5.5,
    maxZoom: 15,
    pitch: 0,
    bearing: 0
  });

  const onViewStateChange = useCallback(({viewState}) => {
    // Manipulate view state
    // viewState.target[0] = Math.min(viewState.target[0], 10);
    console.log("zoom")
    console.log(viewState.zoom)
    // Save the view state and trigger rerender
    setInitialViewState(viewState);
  }, []);
  // const {arcs, targets, sources} = useMemo(() => getLayerData(data), [data]);
  const {arcs, targets, sources} = useMemo(() => data, [data])
  // console.log(arcs)
  // console.log(targets)
  // Arcs (edge): [{target: Array(3), source: Array(3), value: Number}]
  // Targets (node): [{gain: Number, loss: Number, position: Array(3), net: Number, name: String}]
  // Sources (edge): [{position: Array(3), target: Array(3), name: String, radius: Number, gain: Number}]
  const radiusScale = scalePow().exponent(3).domain([initialViewState.maxZoom, 5]).range([10, 550])(initialViewState.zoom)
  console.log(radiusScale)

  const layers = arcs &&
    targets && [
      // new ScatterplotLayer({
      //   id: 'sources',
      //   data: sources,
      //   brushingRadius: brushRadius,
      //   brushingEnabled: enableBrushing,
      //   // only show source points when brushing
      //   radiusScale: enableBrushing ? 3000 : 0,
      //   getFillColor: d => (d.gain > 0 ? TARGET_COLOR : SOURCE_COLOR),
      //   extensions: [brushingExtension]
      // }),
      new ScatterplotLayer({
        id: 'targets-base',
        data: targets,
        brushingRadius: brushRadius,
        brushingEnabled: false,
        pickable: true,
        radiusScale: radiusScale,
        getFillColor: TARGET_COLOR,
      }),
      new ScatterplotLayer({
        id: 'targets-ring',
        data: targets,
        brushingRadius: brushRadius,
        lineWidthMinPixels: 2,
        stroked: true,
        filled: false,
        brushingEnabled: enableBrushing,
        radiusScale: enableBrushing ? 1000 : 0, // only show rings when brushing
        getLineColor: SOURCE_COLOR,
        extensions: [brushingExtension]
      }),
      new ScatterplotLayer({
        id: 'targets',
        data: targets,
        brushingRadius: brushRadius,
        brushingEnabled: enableBrushing,
        pickable: true,
        radiusScale: 500,
        getFillColor: SOURCE_COLOR,
        extensions: [brushingExtension]
      }),
      new HeatmapLayer({
        data: targets,
        id: 'heatmap',
        pickable: false,
        getPosition: d => d.position,
        getWeight: d => d.net,
        radiusPixels: 100,
        intensity: 1,
        threshold: 0.02
      }),
      new ArcLayer({
        id: 'arc-reverse',
        data: arcs,
        getWidth: strokeWidth,
        opacity,
        brushingRadius: brushRadius,
        brushingEnabled: enableBrushing,
        getSourcePosition: d => d.target,
        getTargetPosition: d => d.source,
        getSourceColor: SOURCE_COLOR,
        getTargetColor: TARGET_COLOR,
        extensions: [brushingExtension]
      }),
      new ArcLayer({
        id: 'arc',
        data: arcs,
        getWidth: strokeWidth,
        opacity,
        brushingRadius: brushRadius,
        brushingEnabled: enableBrushing,
        getSourcePosition: d => d.source,
        getTargetPosition: d => d.target,
        getSourceColor: SOURCE_COLOR,
        getTargetColor: TARGET_COLOR,
        extensions: [brushingExtension]
      })
    ];


  return (
    <DeckGL
      layers={layers}
      viewState={initialViewState}
      onViewStateChange={onViewStateChange}
      controller={true}
      getTooltip={getTooltip}
    >
      <div>
        <p>Hello</p>
      </div>
      <StaticMap
        reuseMaps
        mapStyle={mapStyle}
        preventStyleDiffing={true}
        mapboxApiAccessToken={MAPBOX_TOKEN}
      />
    </DeckGL>
  );
}
